import React, {useEffect, useState} from "react";
import { useHistory, useParams } from "react-router-dom";
import styled from "styled-components";
import "./Detail.scss";

function Detail(props){
    let { id } = useParams();
    let [inputData,setInputData] = useState('');
    let [modalActive, setModalActive] = useState(true);
    let product = props.shoes.find(function(item){
        return item.id == id
    })
    let history = useHistory();

    useEffect(() => {
        let timer = setTimeout(() => {
            setModalActive(false);
        },3000);
        console.log('trigger useEffect!!');
        return () => {
             console.log('unmount Event');
             clearTimeout(timer);
        }
    },[modalActive]);

    return(
        <div className="container">
            <Box>
                <Title className="red" color="blue">Detail</Title>
            </Box>

            <input type="text" onChange={(e)=>{setInputData(e.target.value)}}/>

            {
                modalActive === true
                ? <Alert/>
                : null
            }
            <div className="row">
                <div className="col-md-6">
                    <img src="https://codingapple1.github.io/shop/shoes1.jpg" width="100%" />
                </div>
                <div className="col-md-6 mt-4">
                    <h4 className="pt-5">{product.title}</h4>
                    <p>{product.content}</p>
                    <p>{product.price}원</p>
                    <Info stock={props.stock} id={product.id}></Info>
                    <button className="btn btn-danger" onClick={() => {
                        let setStockArr = [...props.stock];
                        setStockArr[product.id] = props.stock[product.id] - 1;
                        props.setStock(setStockArr);
                    }}>주문하기</button>
                    <button
                        className="btn btn-danger ml-1"
                        onClick={() => {
                            //history.goBack();
                            history.push('/');
                        }}>뒤로가기</button>
                </div>
            </div>
        </div>
    )
}

function Info(props){
    return(
        <p>재고 : {props.stock[props.id]}개</p>
    )
}

function Alert(){
    return(
        <div className="my-alert2">
            <p>재고가 얼마 남지 않았습니다.</p>
        </div>
    )
}

const Box = styled.div`
  padding: 20px;
  margin-top: 40px;
`

const Title = styled.h4`
  font-size: 25px;
  color: ${props => props.color};
`

export default Detail;